<script lang="coffeescript">
  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  import Progress from "./_Progress.svelte"
  import tippy from 'tippy.js'

  
  export style = undefined
  export item = undefined
  export value = { percent_complete: 0, color: "light-gray" }
  export size = 24

  tippyObject = undefined
  component = undefined
  isShowing = false


  export idAttribute = "id"

  import {IconProgressCircle} from "../../../Braintapper"
  
  

  



  popover = (element, content) ->

    tippyOptions =
      theme: "popover"
      trigger: "click"
      placement: "bottom-end"
      interactive: true
      popperOptions:
        modifiers: [
          {
            name: "flip"
            options:
              fallbackPlacements: ["top","right","left"]
          }

        ]


    if (typeof content == 'function')

      tippyOptions.onCreate = (instance) ->
        component = new content(target: instance.popper.querySelector('.tippy-content'))
        component.$set { isShowing: isShowing } #  dateValue: newValue,


        component.$on "set-progress",  (e) ->
          component.$set { progressValue: e.detail }
          eventObject = {  progress: e.detail, ancestry: item.ancestry }
          eventObject[idAttribute] = item[idAttribute]
          dispatch "update", eventObject
          tippyObject.hide()
        return

      tippyOptions.onHide = (instance) ->
        #console.error "TIPPY HIDE"
        isShowing = false
        component.$set { isShowing: isShowing }

      tippyOptions.onShow = (instance) ->
        #console.error "TIPPY SHOW"
        isShowing = true
        component.$set { isShowing: isShowing }


    else
      tippyOptions.content = content

    if Object.keys(tippyOptions).length == 0
      tippyObject = tippy(element)
    else
      tippyObject = tippy(element, tippyOptions)
    return
    {
      update: (params) ->
        console.error "update"
        #if component
        #  component.$set { dateValue: newValue }

        return
      destroy: ->
        tippyObject.destroy()
        console.error "tippy destroy"
        #if component
          # debugger
        #  component.$destroy()
        return

    }


</script>



<div use:popover={Progress} is-showing={isShowing} {style}>
  {#if item.progress }
  <IconProgressCircle bind:value={item.progress.percent_complete} bind:color={item.progress.color} {size}/>
  {/if}
</div>
